___CSS_LOADER_EXPORT___.push([module.id, `.active {
    background-color: #1D4ED8;
}

.nav-admin-gestion {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #121f30;
    position: sticky;
    width: 100%;
}

.nav-admin-gestion .title {
    font-size: 1.5rem;
    margin: 1rem;
    font-weight: bold;
    text-decoration: none;
    color: #FFF;
}

.nav-admin-gestion ul{
    /* display: none; */
    display: flex;
}

.nav-admin-gestion ul.open {
    display: flex;
}

.nav-admin-gestion ul li {
    list-style: none;
}

.nav-admin-gestion ul li a{
    display: block;
    text-decoration: none;
    color: #FFF;
    padding: 0.5rem;
    margin: 0 0.5rem;
    border-radius: 0.5rem;
}

.nav-admin-gestion ul li a:not(.active):hover {
    background-color: #296568;
}

.active {
    background-color: #124152;
}


.nav-admin-gestion .menu {
    display: none;
    position: absolute;
    top: 0.75rem;
    right: 0.5rem;
    flex-direction: column;
    justify-content: space-between;
    width: 2.25rem;
    height: 2rem;
}

.nav-admin-gestion .menu span {
    height: 0.4rem;
    width: 100%;
    background-color: #FFF;
    border-radius: 0.2rem;
}

@media (max-width: 480px) {
    .nav-admin-gestion .menu {
        display: flex;
    }

    .nav-admin-gestion {
        flex-direction: column;
        align-items: start;
    }

    .nav-admin-gestion ul {
        display: none;
        flex-direction: column;
        width: 100%;
        margin-bottom: 0.25rem;
    }

    .nav-admin-gestion ul li {
        width: 100%;
        text-align: center;
    }

    .nav-admin-gestion ul li a {
        margin: 0.2rem 0.5rem;
    }
}   `, "",{"version":3,"sources":["webpack://./src/components/GestionUsuarios/header/Header.css"],"names":[],"mappings":"AAAA;IACI,yBAAyB;AAC7B;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,yBAAyB;IACzB,gBAAgB;IAChB,WAAW;AACf;;AAEA;IACI,iBAAiB;IACjB,YAAY;IACZ,iBAAiB;IACjB,qBAAqB;IACrB,WAAW;AACf;;AAEA;IACI,mBAAmB;IACnB,aAAa;AACjB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,cAAc;IACd,qBAAqB;IACrB,WAAW;IACX,eAAe;IACf,gBAAgB;IAChB,qBAAqB;AACzB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,yBAAyB;AAC7B;;;AAGA;IACI,aAAa;IACb,kBAAkB;IAClB,YAAY;IACZ,aAAa;IACb,sBAAsB;IACtB,8BAA8B;IAC9B,cAAc;IACd,YAAY;AAChB;;AAEA;IACI,cAAc;IACd,WAAW;IACX,sBAAsB;IACtB,qBAAqB;AACzB;;AAEA;IACI;QACI,aAAa;IACjB;;IAEA;QACI,sBAAsB;QACtB,kBAAkB;IACtB;;IAEA;QACI,aAAa;QACb,sBAAsB;QACtB,WAAW;QACX,sBAAsB;IAC1B;;IAEA;QACI,WAAW;QACX,kBAAkB;IACtB;;IAEA;QACI,qBAAqB;IACzB;AACJ","sourcesContent":[".active {\r\n    background-color: #1D4ED8;\r\n}\r\n\r\n.nav-admin-gestion {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    background-color: #121f30;\r\n    position: sticky;\r\n    width: 100%;\r\n}\r\n\r\n.nav-admin-gestion .title {\r\n    font-size: 1.5rem;\r\n    margin: 1rem;\r\n    font-weight: bold;\r\n    text-decoration: none;\r\n    color: #FFF;\r\n}\r\n\r\n.nav-admin-gestion ul{\r\n    /* display: none; */\r\n    display: flex;\r\n}\r\n\r\n.nav-admin-gestion ul.open {\r\n    display: flex;\r\n}\r\n\r\n.nav-admin-gestion ul li {\r\n    list-style: none;\r\n}\r\n\r\n.nav-admin-gestion ul li a{\r\n    display: block;\r\n    text-decoration: none;\r\n    color: #FFF;\r\n    padding: 0.5rem;\r\n    margin: 0 0.5rem;\r\n    border-radius: 0.5rem;\r\n}\r\n\r\n.nav-admin-gestion ul li a:not(.active):hover {\r\n    background-color: #296568;\r\n}\r\n\r\n.active {\r\n    background-color: #124152;\r\n}\r\n\r\n\r\n.nav-admin-gestion .menu {\r\n    display: none;\r\n    position: absolute;\r\n    top: 0.75rem;\r\n    right: 0.5rem;\r\n    flex-direction: column;\r\n